"use client"

import * as React from "react"
import { Moon, Sun, Monitor } from "lucide-react"
import { useTheme } from "next-themes"

export function ThemeToggle() {
  const { theme, setTheme } = useTheme()
  const [mounted, setMounted] = React.useState(false)

  // Avoid hydration mismatch
  React.useEffect(() => {
    setMounted(true)
  }, [])

  if (!mounted) return null

  return (
    <div className="flex items-center gap-1 p-1 bg-muted/50 border border-border/50 rounded-lg backdrop-blur-sm">
      <button
        onClick={() => setTheme("light")}
        className={`p-2 rounded-md transition-all ${
          theme === "light" ? "bg-background text-emerald-500 shadow-sm" : "text-zinc-500 hover:text-zinc-300"
        }`}
        title="Light Mode"
      >
        <Sun size={14} />
      </button>
      <button
        onClick={() => setTheme("dark")}
        className={`p-2 rounded-md transition-all ${
          theme === "dark" ? "bg-background text-emerald-500 shadow-sm" : "text-zinc-500 hover:text-zinc-300"
        }`}
        title="Dark Mode"
      >
        <Moon size={14} />
      </button>
      <button
        onClick={() => setTheme("system")}
        className={`p-2 rounded-md transition-all ${
          theme === "system" ? "bg-background text-emerald-500 shadow-sm" : "text-zinc-500 hover:text-zinc-300"
        }`}
        title="System Preference"
      >
        <Monitor size={14} />
      </button>
    </div>
  )
}
